.popup-bg {
    position: fixed; //задаем fixed для того, что бы затемнение было на весь экран, не зависимо от скролла.
    top: 0; // позиция сверху 0
    left: 0; // слева 0
    width: 100%; // ширина 100% экрана
    height: 100%; // высота 100%
    background: rgba(110, 156, 159, 0.95); // фон черный, с прозрачностью 0.8 для затемнения сайта
    display: none; // скрываем по умолчанию, будем показывать при клике
    // на время работы можете не устанавливать это значние, что бы видеть результат
    z-index: 9999999;
}


.popup {
    position: absolute; // позиционируем само окно абсолютно, относительно фона
    background: #ffffff; // белый фон окна
    width: 100%;
    max-width: 491px;
    height: 100%;
    max-height: 503px;
     // ширина в 400px
    left: 50%; // отступ слева 50%
    top: 50%; // отступ сверху 50%
    transform: translate(-50%, -50%); // иделаьно центрируем
    // padding: 30px; // делаем отступы внутрь со всех сторон
    padding-top: 70px; // увеличиваем отступ сверху, что бы крестик не налазил на контент
    @media(max-width: 500px) {
      max-height: 400px;
      padding-top: 40px;
    }
}

.popup__box{
    .form{
        flex-direction: column;
        display: flex;
        padding: 20px;
        span{
            font-weight: 500;
            font-size: 25px;
            color: $black;
            text-align: center;
        }
        input{
            font-weight: 500;
            font-size: 17px;
            color: #868686;
            width: 100%;
            max-width: 350px;
            margin: 0 auto;
            border: 0;
            border-bottom: 1px solid $black;
            padding-bottom: 16px;
            margin-top: 35px;
            outline: 0;
            @media(max-width: 500px) {
              margin-top: 20px;
            }
        }
        button{
            font-weight: 400;
            font-size: 17px;
            width: 100%;
            max-width: 350px;
            height: 68px;
            margin: 0 auto;
            display: flex;
            justify-content: center;
            align-items: center;
            background: #6E9C9F;
            color: $white;
            margin-top: 35px;
            border: 0;
            @media(max-width: 500px) {
              margin-top: 20px;
            }
        }
    }
}

.close-popup{
    position: absolute;
    top: 32px;
    right: 32px;
    font-size: 44px;
    color: #222;
    cursor: pointer;
    @media(max-width: 500px) {
      top: 20px;
      right: 20px;
    }

}


