.more{
    padding-bottom: 6.842vw;
    .more__wrapper{
        min-width: 0;
        @media(max-width: 768px){
            flex-wrap: wrap;
        }
        h2{
            font-weight: 500;
            font-size: $s_40;
            color: $black;
            padding-top: 6.842vw;
            padding-bottom: 4.842vw;
        }
        .more__box{
            display: flex;
            justify-content: space-between;
            min-width: 0;
            @media(max-width: 768px){
                flex-wrap: wrap;
                flex-direction: column-reverse;
            }
            .slider{
                min-width: 0;
                display: flex;
                justify-content: space-between;
                width: 60%;
                position: relative;
                flex-direction: column;
                @media(max-width: 768px){
                    margin: 0 auto;
                }
                @media(max-width: 768px){
                    width: 90%;
                }
                @media(max-width: 768px){
                    padding-top: 20px;
                }
                img{
                    width: 100%;
                    max-width: 729px;
                }
            }
            .more__insider{
                @media(max-width: 1300px){
                    padding-right: 50px;
                }
                @media(max-width: 900px){
                    padding-right: 20px;
                    padding-left: 30px;
                }
                @media(max-width: 768px){
                    margin: 0 auto;
                }
                h3{
                    font-weight: 500;
                    font-size: $s_25;
                    color: $black;
                    padding-top: 8.947vw;
                    padding-bottom: 1.474vw;
                    @media(max-width: 1050px){
                        padding-top: 4.947vw;
                    }
                    @media(max-width: 725px){
                        padding-top: 0;
                    }
                }
                p{
                    font-weight: 500;
                    font-size: $s_17;
                    line-height: 140%;
                    padding-bottom: 1.474vw;
                }
                .more__btn{
                    .more__button{
                        font-weight: 500;
                        font-size: $s_17;
                        color: #6E9C9F;
                        letter-spacing: 0.02em;
                        border: 0;
                        background: $white;
                        padding-bottom: 5px;
                        transition: all .5s ease-in-out;
                        &:hover{
                            border-bottom: 1px solid #CEDEDF;
                        }
                    }
                }
            }
        }
    }
}
