.footer{
    background: #F1EADC;
    padding-top: 5.474vw;
    padding-bottom: 5.105vw;
    .footer__global{
        display: flex;
        justify-content: space-between;
        @media(max-width: 725px){
           flex-wrap: wrap;
        }
        .footer__wrapper{
            flex-direction: column;
            .footer_img{
                width: 100%;
                max-width: 139px;
                margin-right: 7.684vw;
            }
            @media(max-width: 725px){
                flex-wrap: wrap;
             }
            .footer__box{
                display: flex;
                .footer__left{
                    padding-right: 9.895vw;
                    padding-top: 3.684vw;
                    li{
                        padding-bottom: 6px;
                        a{
                            font-weight: 400;
                            font-size: $s_13;
                            color: $black;
                        }
                    }
                }
                .footer__right{
                    padding-top: 2.053vw;
                    li{
                        padding-bottom: 10px;
                        a{
                            font-weight: 400;
                            font-size: $s_13;
                            color: $black;
                            &:hover{
                              color: #6E9C9F;
                              transition: all 0.3s ease-in;
                            }
                        }
                    }
                }
            }
            .footer__nav{
                display: flex;
                padding-top: 20px;
                li{
                    padding-bottom: 10px;
                }
                .footer__item{
                    .footer__link{
                        padding-right: 2.368vw;
                        font-weight: 400;
                        font-size: $s_15;
                        color: $black;
                        &:hover{
                            color: #6E9C9F;
                            transition: all 0.3s ease-in;
                        }
                    }
                }
            }
        }
        .footer__insider{
            text-align: end;
            @media(max-width: 505px){
                text-align: start;
                padding-top: 10px;
            }
            li{
                padding-bottom: 31px;
                &:first-child{
                    padding-bottom: 9px;
                }
                img{
                    padding-left: 12px;
                }
            }
        }
    }
}
