.collection{
    .collection__wrapper{
        h2{
            font-weight: 500;
            font-size: $s_40;
            color: $black;
            padding-top: 9.684vw;
            padding-bottom: 4.842vw;
        }
        .collection_group{
            display: flex;
            justify-content: center;
            .collection__button{
                font-weight: 400;
                font-size: 17px;
                background: $white;
                color: #6E9C9F;
                border: 1px solid #6E9C9F;
                margin-top: 3.421vw;
                padding-top: 22px;
                padding-bottom: 22px;
                padding-left: 55px;
                padding-right: 55px;
                @media(max-width: 600px){
                    padding-top: 15px;
                    padding-bottom: 15px;
                    padding-left: 40px;
                    padding-right: 40px;
                }
                &:hover{
                    background: #6E9C9F;
                    color: $white;
                    transition: all .5s ease;
                }
            }
        }
        .collection__box{
            display: flex;
            justify-content: space-between;
            @media(max-width: 768px){
                flex-wrap: wrap;
            }
            .collection__block{
                padding-right: 20px;
                width: 100%;
                transition: all .5s ease;
                @media(max-width: 768px){
                    flex-wrap: wrap;
                    text-align: center;
                    padding-right: 0;
                }
                .collection_insider{
                    position: relative;
                    transition: all .5s ease-in;
                    @media(max-width: 768px){
                       display: flex;
                       justify-content: center;
                       padding-top: 20px;
                       &:first{
                            padding-top: 0;
                       }
                    }
                    &:hover{
                        transition: all .5s ease;
                        cursor: pointer;
                        &::after{
                            content:'';
                            background: url(./img/gallery_hover.svg) no-repeat;
                            background-size: cover;
                            width: 100%;
                            max-width: 350px;
                            height: 100%;
                            max-height: 478px;
                            display: block;
                            position: absolute;
                            top: 0;
                            transition: all .5s ease;
                            cursor: pointer;
                        }
                    }
                    img{
                        width: 100%;
                        max-width: 350px;
                        transition: all .5s ease;
                    }
                }
                span{
                    font-weight: 500;
                    font-size: $s_20;
                    margin-top: 1.263vw;
                    padding-bottom: 9px;
                    display: block;
                    padding-top: 1.1vw;
                }
                p{
                    font-weight: 500;
                    font-size: 15px;
                    color: #998E78;
                }
            }
        }
    }
}
