.magic{
  .magic__wrapper{
    display: flex;
    justify-content: space-between;
    padding-bottom: 6.7vw;
    @media (max-width: 880px) {
      flex-wrap: wrap;
    }
    img{
      width: 100%;
      max-width: 445px;
      margin: 0 auto;
      @media(max-width: 880px) {
        padding-top: 45px;
      }
    }
    .magic__box{
      padding-right: 20px;
      margin: 0 auto;
      @media(max-width: 880px) {
        padding-right: 0;
      }
      h3{
        padding-bottom: 2.39vw;
        font-weight: 500;
        font-size: 25px;
        color: $black;
      }
      span{
        font-weight: 500;
        font-size: $s_17;
        line-height: 140%;
        @media (max-width: 930px) {
          br{
            display: none;
          }
        }
        @media (max-width: 880px) {
          br{
            display: flex;
          }
        }
        @media (max-width: 576px) {
          br{
            display: none;
          }
        }
      }
      p{
        font-weight: 500;
        font-size: $s_17;
        line-height: 140%;
        padding-top: 1.5625vw;
        @media (max-width: 930px) {
          br{
            display: none;
          }
        }
        @media (max-width: 880px) {
          br{
            display: flex;
          }
        }
        @media (max-width: 576px) {
          br{
            display: none;
          }
        }
      }
    }
  }
  .magic__btn{
    display: flex;
    justify-content: center;
    .magic__button{
      padding-top: 22px;
      padding-bottom: 22px;
      padding-left: 50px;
      padding-right: 50px;
      background: #6E9C9F;
      border: 0;
      font-size: $s_17;
      color: $white;
      margin-bottom:  6.7vw;
    }
  }
}
