.important{
    .important__wrapper{
        h2{
            font-weight: 500;
            font-size: $s_40;
            color: $black;
            padding-top: 7.368vw;
            padding-bottom: 4.789vw;
        }
        .important__box{
            display: flex;
            justify-content: space-between;
            @media(max-width: 640px){
                flex-wrap: wrap;
                justify-content: center;
                text-align: center;
            }
            .important__insider{
                display: flex;
                flex-direction: column;
                @media(max-width: 907px){
                    padding-right: 20px;
                    &:last-child{
                        padding-right: 0;
                    }
                }
                @media(max-width: 640px){
                    padding-top: 20px;
                    &:first-child{
                        padding-top: 0;
                    }
                }
                img{
                    width: 100%;
                    max-width: 60px;
                    padding-bottom: 1.947vw;
                    @media(max-width: 640px){
                       margin:  0 auto;
                    }
                }
                span{
                    font-weight: 500;
                    font-size: $s_25;
                    color: $black;
                    padding-bottom: 1.263vw;
                }
                p{
                    font-weight: 500;
                    font-size: $s_17;
                    color: $black;
                    @media(max-width: 907px){
                        br{
                            display: none;
                        }
                    }
                    @media(max-width: 640px){
                        br{
                            display: flex;
                        }
                    }
                }
            }
        }
    }
}