/* Основные стили */

/* Слайдер */
.slick-slider{
	min-width: 0;
}

// .slick-slide{

// }
/* Ограничивающая оболочка */
.slick-list {
	overflow: hidden;
}
/* Лента слайдов */
.slick-track{
	display: flex;
	align-items: flex-start;
}
.slick-arrow{
    &.slick-prev{
        background: url(./img/carusel_vector_1.svg) no-repeat 50% 50%;
        width: 50px;
        height: 30px;
        position: absolute;
        top: 50%;
        left: -17px;
        border: 0;
        font-size: 0;
        @media(max-width: 900px){
            opacity: 0;
        }
        @media(max-width: 725px){
            opacity: 1;
        }
    }
}
.slick-arrow{
    &.slick-next{
        background: url(./img/carusel_vector_2.svg) no-repeat 50% 50%;
        width: 50px;
        height: 30px;
        position: absolute;
        top: 50%;
        right: -70px;
        border: 0;
        font-size: 0;
        @media(max-width: 900px){
            opacity: 0;
        }
        @media(max-width: 725px){
            opacity: 1;
        }
        @media(max-width: 725px){
            right: -28px;
        }
    }
}
.slider{
    .slick-dots{
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        bottom: 77px;
        @media(max-width: 725px){
            bottom: 43px;
        }
       li{
            list-style: none;
            &.slick-active{
                button{
                    background: $white;
                }
            }
            button{
                font-size: 0;
                background: rgba(255, 255, 255, 0.36);
                margin-right: 10px;
                height: 4px;
                width: 30px;
                border: 0;
            }
       }
    }
}
.admission__slaider__main{
    .slick-dots{
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        bottom: -100px;
        @media(max-width: 900px){
            bottom: -40px;
        }
        @media(max-width: 670px){
            bottom: -20px;
        }
       li{
            list-style: none;
            &.slick-active{
                button{
                    background: $black;
                }
            }
            button{
                font-size: 0;
                background: #D1D1D1;
                margin-right: 10px;
                height: 4px;
                width: 30px;
                border: 0;
            }
       }
    }
}

