.woman{
  padding-bottom: 5.625vw;
  .woman__wrapper{
    .woman__box{
      display: flex;
      @media (max-width: 880px) {
      flex-wrap: wrap;
      flex-direction: column-reverse;
      }
      img{
        width: 100%;
        max-width: 442px;
        padding-right: 4.895vw;
        margin: 0 auto;
        padding-top: 20px;
        @media(max-width: 880px) {
          padding-right: 0;
        }
      }
      .woman__insider{
        margin: 0 auto;
        h3{
          font-weight: 500;
          font-size: 25px;
          color: $black;
          padding-bottom: 2.395vw;
        }
        span{
          font-weight: 500;
          font-size: $s_17;
          line-height: 140%;
          @media (max-width: 930px) {
            br{
              display: none;
            }
          }
          @media (max-width: 880px) {
            br{
              display: flex;
            }
          }
          @media (max-width: 576px) {
            br{
              display: none;
            }
          }
        }
        p{
          font-weight: 500;
          font-size: $s_17;
          line-height: 140%;
          padding-top: 1.5625vw;
          @media (max-width: 930px) {
            br{
              display: none;
            }
          }
          @media (max-width: 880px) {
            br{
              display: flex;
            }
          }
          @media (max-width: 576px) {
            br{
              display: none;
            }
          }
        }
      }
    }
    .woman__row{
      h2{
        padding-top: 8.125vw;
        padding-bottom:  1.352vw;
      }
      p{
        padding-bottom: 11.145vw;
      }
    }
  }
}
