.connection{
  .connection__wrapper{
    .connection__box{
      display: flex;
      @media(max-width: 715px){
        flex-wrap: wrap;
      }
      li{
        padding-bottom: 15px;
        font-weight: 500;
        font-size: 20px;
        color: $black;
      }
      ul{
        padding-top: 10.833vw;
        padding-bottom: 10.833vw;
        @media(max-width: 715px){
          padding-bottom: 5vw;
          padding-top: 5vw;
         &:last-child{
          padding-top: 0;
         }
        }
        @media(max-width: 369px){
         &:nth-child(2){
          padding-top: 0;
         }
        }
        &:first-child{
          padding-right: 9.250vw;
        }
        &:nth-child(2){
          padding-right: 8.167vw;
        }
      }
    }
  }
}
