.admission{
    .admission__01{
        width: 100%;
        max-width: 42.105vw;
        height: 845px;
        position: absolute;
        top: 0;
        right: 0;
        z-index: -1;
    }
    .admission__wrapper{
        display: flex;
        justify-content: space-between;
        position: relative;
        @media(max-width: 670px){
            flex-direction: column;
        }
        .admission__03{
            position: absolute;
            top: 110px;
            right: 15px;
            width: 100%;
            max-width: 197px;
            z-index: 100;
            @media(max-width: 1374px){
                max-width: 160px
            }
            @media(max-width: 1025px){
                max-width: 130px;
                top: 27px;
                right: 9px;
            }
            @media(max-width: 900px){
                max-width: 100px;
            }
            @media(max-width: 670px){
                top: 370px;
                right: 25vw;
            }
            @media(max-width: 600px){
              right: 15vw;
            }
            @media(max-width: 500px){
                right: 17vw;
            }
            @media(max-width: 500px){
                right: 9vw;
            }
            @media(max-width: 390px){
              right: 6vw;
          }
        }
        .admission__04{
            position: absolute;
            right: 26vw;
            bottom: -4.3vw;
            width: 100%;
            max-width: 190px;
            z-index: 100;
            @media(min-width: 1804px){
              right: 20vw;
            }
            @media(max-width: 1374px){
                max-width: 150px;
                right: 30vw;
            }
            @media(max-width: 1025px){
                max-width: 127px;
            }
            @media(max-width: 900px){
                max-width: 117px;
            }
            @media(max-width: 900px){
                max-width: 100px;
            }
            @media(max-width: 670px){
                right: 66vw;
            }
        }
        .admission__slaider__main{
            flex-direction: column;
            width: 50%;
            padding-left: 94px;
            @media(max-width: 1200px){
                padding-left:  15px;
            }
            @media(max-width: 670px){
                margin: 0 auto;
                width: 70%;
            }
            @media(max-width: 500px){
                margin: 0 auto;
                width: 100%;
                padding-right: 20px;
            }
            h1{
                font-weight: 500;
                font-size: $s_55;
                line-height: 110%;
                color: $black;
                padding-top: 7.632vw;
                padding-bottom: 2.368vw;
                @media(max-width: 900px){
                    font-size: 2.44rem;
                }
                @media(max-width: 670px){
                    padding-top: 4.632vw;
                }
            }
            p{
                font-weight: 400;
                font-size: $s_20;
                line-height: 140%;
                color: $black;
                padding-bottom: 2.737vw;
                text-align: end;
                width: 100%;
                max-width: 370px;
                @media(max-width: 900px){
                    font-size: 1rem;
                }
            }
            .novelties__box{
                display: flex;
                justify-content: end;
                width: 100%;
                max-width: 370px;
                .novelties__button{
                    font-weight: 400;
                    font-size: $s_17;
                    color: $white;
                    padding-top: 22px;
                    padding-bottom: 22px;
                    padding-left: 50px;
                    padding-right: 50px;
                    background: #6E9C9F;
                    border: 0;
                    @media(max-width: 900px){
                        padding-top: 15px;
                        padding-bottom: 15px;
                        padding-left: 40px;
                        padding-right: 40px;
                    }
                }
                .novelties__btn{
                    height: 67px;
                    width: 67px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    border: 0;
                    @media(max-width: 900px){
                        height: 50px;
                        width: 50px;
                    }
                    img{
                        width: 100%;
                        max-width: 14px;
                        height: 28px;
                    }
                }
            }
        }
        .admission__sub__slaider{
            width: 34%;
            position: relative;
            padding-right: 50px;
            margin-right: 55px;
            @media(max-width: 1025px){
                margin-right: 10px;
            }
            @media(max-width: 670px){
                margin: 0 auto;
                padding-top: 40px;
                padding-right: 0;
                width: 40%;
            }
            @media(max-width: 600px){
                width: 55%;
            }
            @media(max-width: 500px){
                width: 70%;
            }
            .admission_item{
                position: relative;
            }
            .admission__02{
                width: 100%;
                max-width: 410px;
                @media(max-width: 1374px){

                }
            }
        }
    }
}
