.shop{
    .shop__wrapper{
      padding-bottom: 6vw;
      .shop__pagination{
        .shop__posts{
          display: flex;
          justify-content: center;
          flex-wrap: wrap;
          .post{
            text-align: center;
            .collection_insider{
              img{
                width: 100%;
                max-width: 350px;
                transition: all .5s ease;
              }
            }
            span{
              font-weight: 500;
              font-size: $s_20;
              margin-top: 1.263vw;
              padding-bottom: 9px;
              display: block;
              padding-top: 1.1vw;
            }
            p{
              font-weight: 500;
              font-size: 15px;
              color: #998E78;
            }
          }
        }
      }
        .shop__box{
            h2{
                font-weight: 500;
                font-size: 55px;
                color: $black;
                padding-top: 9.389vw;
                padding-bottom: 2.301vw;
            }
            p{
                font-weight: 500;
                font-size: 17px;
                padding-bottom: 12.938vw;
                span{
                    color: #919191;
                }
            }
        }
    }
}
