.header{
    height: 140px;
    align-items: center;
    display: flex;
    @media(max-width: 1025px){
        height: 100px;
    }
    @media(max-width: 670px){
        height: 70px;
    }
    .header__wrapper{
        display: flex;
        justify-content: space-between;
        .header__logo{
            img{
                width: 100%;
                max-width: 139px;
                @media(max-width: 900px){
                    display: none;
                }
            }
        }
        .header__box{
            width: 100%;
            display: flex;
            justify-content: space-between;
            position: fixed;
            top: 35px;
            z-index: 1000;
            display: none;
            @media(max-width: 900px){
                display: flex;
            }
            .header__img{
                order: 1;
                margin: 0 auto;
                display: flex;
                img{
                    width: 100%;
                    max-width: 139px;
                    z-index: 100;
                }
            }
            .header__kor__none{
                img{
                    z-index: 100;
                    display: none;
                    @media(max-width: 900px){
                        display: flex;
                    }
                }
            }
        }
        .menu{
            display: flex;
            align-items: center;
            .menu__contact{
                display: flex;
                align-items: center;
                li{
                    &:first-child{
                        padding-left: 9.211vw;
                        @media(max-width: 900px){
                            padding-left: 0;
                        }
                    }
                    &:last-child{
                        padding-left: 3.526vw;
                        @media(max-width: 900px){
                            padding-left: 0;
                        }
                    }
                    a{
                        font-weight: 400;
                        font-size: 15px;
                        color: $black;
                    }
                }
                .menu__telephone{
                    width: 100%;
                    max-width: 14.45px;
                    margin-right: 16px;
                    display: flex;
                    align-items: center;
                }
                .menu__basket{
                    width: 100%;
                    max-width: 24px;
                }
            }
            .menu__nav{
                display: flex;
                .menu__item{
                    margin-left: 55px;
                    @media(max-width: 1000px){
                      margin-left: 35px;
                    }
                    @media(max-width: 900px){
                        padding-left: 0;
                        margin-left: 0;
                    }
                    .link{
                        font-weight: 400;
                        font-size: $s_17;
                        color: $black;
                        &:hover{
                          color: #6E9C9F;
                          transition: all 0.3s ease-in;
                        }
                      }
                }
            }
        }
    }
}



