.header{
    .header__wrapper{
        .header_hamburger{
            display: none;
            span{
                @media (max-width: 900px) {
                    width: 100%;
                    height: 2px;
                    margin-bottom: 3px;
                    margin-top: 3px;
                    background: #222;
                    transition: all .3s ease;
                    z-index: 2000;
                }
            }

            @media (max-width: 900px) {
                width: 35px;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                border: 0;
                background: transparent;
                border-radius: 4px;
                overflow: hidden;
                margin-right: 30px;
                padding: 0;
                order: 4;
            }
            &.open{
                display: flex;
                right: 20px;
                top: 60px;
                z-index: 10000;
                margin-top: -5px;
                @media (max-width: 900px) {
                    top: 30px;
                }
                span{
                    @media (max-width: 900px) {
                        background: $black;
                    }
                    &:first-child{
                        @media (max-width: 900px) {
                            transform: rotate(36deg) translate3D(3px, -3px, 0);
                            transform-origin: 0 0;
                        }
                    }

                    &:nth-child(2) {
                        @media (max-width: 900px) {
                            transform: translateX(-140%);
                            transform-origin: 0 0;
                        }
                    }

                    &:last-child {
                        @media (max-width: 900px) {
                            transform: rotate(-36deg) translate3D(0px, 5px, 0);
                            transform-origin: 0 100%;
                        }
                    }
                }
            }
        }
        .menu{
            @media (max-width: 900px) {
                display: flex;
                width: 100%;
                position: fixed;
                z-index: 1000;
                top: -125%;
                left: 0;
                display: flex;
                background: #fadcd2e4;
                margin-top: 0;
                flex-direction: column;
                box-shadow: 0px 0px 5px 0px $black;
                padding-top: 16px;
                transition: all .8s ease;
                margin-top: 95px;
                padding-bottom: 40px;
                overflow: auto;
                @media(min-width: 670px){
                    margin-top: 95px;
                }
            }
            &.open{
                @media (max-width: 900px) {
                    top: 0;
                    padding-left: 0;
                    margin-left: 0;
                    padding-top: 30px;
                    flex-direction: column;
                    text-align: center;
                    .menu__nav{
                        flex-direction: column;
                        .menu__item{
                            padding-bottom: 20px;
                            .link{
                                font-size: 36px;
                            }
                        }
                    }
                    .menu__contact{
                        width: 100%;
                        display: flex;
                        justify-content: center;
                        li{
                            a{
                                font-size: 32px;
                            }
                        }
                        .menu__telephone{
                            margin-right: 30px;
                            width: 100%;
                            img{
                                width: 40px;
                                height: 40px;
                            }
                        }
                        .menu__basket__none{
                            display: none;
                        }
                    }
                }
            }
        }
    }
}
