  .preloader__block{
    width: 100%;
    height: 100%;
    background-color: rgba(243, 229, 232, 0.993);
    position: absolute;
    top: 0;
    animation: block 2s ease forwards;
    animation-delay: .6s;
    z-index: 10000;
    img{
      width: 100%;
      max-width:  70px;
      z-index: 10001;
      animation: x 1.7s ease forwards;
      position: absolute;
      top: 50%;
      margin-left: -20px;
    }
  }


@keyframes block {
  100% {
    width: 0;
    transform: translate(-100%);
    transition: all 1s ease;
  }
}

@keyframes x {
  0%{
    transform: rotate(0deg);
    left: 50%;
  }

  50%{
    left: 50%;
  }

  100%{
    position: absolute;
    left: -20%;
    transform:  rotate(380deg);
  }
}
