.posts {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .post {
    padding: 8px;
    border-color: bisque;
    border: 1px;
  }
  
  .pagination {
    display: flex;
    justify-content: center;
  }
  
  .pagination__list {
    display: flex;
    list-style-type: none;  
  }
  
  .pagination__item {
    padding: 8px;
    margin: 8px;
    cursor: pointer;
  }
  
  .pagination__item--active {
    font-weight: 800;
    color: gold;
  }